<template>
    <div>
        <v-card
            max-width="344"
            class="mx-auto"

        >
        <v-dialog v-model="dialog.show" persistent max-width="400">
        <v-card>
            <v-card-title class="headline">{{dialog.status}}</v-card-title>
            <v-card-text>{{dialog.message}}
                <div v-if="dialog.status !='Error'">
                    <p><strong>No. Autorizacion: </strong>{{dialog.authnumber}}</p>
                    <strong>Precio:</strong> {{ solicitud.DatosGenerales.Moneda }} {{solicitud.DetalleDocumentos.Monto}}
                </div>
            </v-card-text>
            <v-card-actions>
            <div class="flex-grow-1"></div>
            <v-btn color="green darken-1" text @click="reloadPage()">Cerrar</v-btn>
            <v-btn color="green darken-1" text @click="reloadPage()">Aceptar</v-btn>
            </v-card-actions>
        </v-card>
        </v-dialog>

        <v-overlay :value="overlay">
            <v-progress-circular indeterminate size="64"></v-progress-circular>
            <p>Cobrando...</p>
        </v-overlay>
            <v-card-title>{{solicitud.ID}}</v-card-title>
            <v-card-text>
                <p>Cantidad documentos: {{solicitud.DetalleDocumentos.Documentos.length}}</p>
                 <h4>Detalle</h4>
                 <ul>
                     <li v-for="(value,name,index) in solicitud.DetalleDocumentos.Documentos" v-bind:key="index">
                         {{value.Documento}}
                     </li>
                 </ul>
            </v-card-text>
            <v-card-text>
               
            </v-card-text>
            <v-card-text><strong>Precio:</strong> {{ solicitud.DatosGenerales.Moneda }} {{solicitud.DetalleDocumentos.Monto}}</v-card-text>
            <v-card-actions>
            <v-btn color="success" v-on:click="makePayment(solicitud)">Cobrar</v-btn>
            </v-card-actions>
        </v-card>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    props:['solicitud'],
    data: () => ({
        overlay: false,
        dialog : {
            show: false,
            message : '',
            status : '',
            authnumber: ''
        },
    }),
    methods: {
       reloadPage(){
            window.location.reload()
        },
        makePayment(solicitud){
            this.overlay = true
            var ts = Math.round((new Date()).getTime() / 1000)
            let numauthpayment = 'MESCYT'+ts
            axios.post('http://des-webservices.serviciosrd.gob.do:84/api/solicitudes/confirmPayment',{NumeroSolicitud:solicitud.ID,NumeroAutorizacion:numauthpayment},{headers: {Authorization : 'Bearer zon3g0UiMbkSU8Lk4JQFjcQ1qOAI6YEbg0sIIVPlhTdKJcjjakDj0HWY8J2v'}})
            .then(response =>{
                if(response.data.status === 'success')
                {
                    console.log('asdasdasdasdas')
                    this.overlay = false
                    this.dialog.show = true
                    this.dialog.message = 'Pago realizado correctamente'
                    this.dialog.status = 'Pago exitoso'
                    this.dialog.authnumber = numauthpayment
                }
                else{
                    this.overlay = false;
                    this.dialog.show = true
                    this.dialog.message = response.data.detail
                    this.dialog.status = 'Error'
                }

            })
            .catch(e => {
                console.log(e)
            })
            .finally(() => {
                this.overlay = false
            })
        }
    }
}
</script>